import { getSupabaseClient } from "./supabase-client"

export const authService = {
  async signUp(email: string, password: string, name: string, role: "patient" | "dentist" | "hr") {
    // Trim and normalize email
    const normalizedEmail = email.trim().toLowerCase()
    
    // Basic email validation - only require @ symbol
    if (!normalizedEmail || !normalizedEmail.includes("@")) {
      throw new Error("Invalid email address format")
    }

    // Use server-side registration API directly (primary method)
    // This avoids Supabase Auth email validation issues and uses the custom auth_users table
    try {
      const response = await fetch("/api/auth/register", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          email: normalizedEmail,
          password: password,
          name: name,
        }),
      })
      
      const result = await response.json()
      
      if (response.ok && result.status === "created") {
        // Server-side registration succeeded
        return { user: result.user }
      } else if (result.status === "exists" || response.status === 409) {
        // User already exists
        throw new Error("User already exists")
      } else {
        // Registration failed
        throw new Error(result.error || "Registration failed")
      }
    } catch (apiError) {
      // If it's "already exists", throw that
      const errorMsg = apiError instanceof Error ? apiError.message : String(apiError)
      if (errorMsg.includes("already exists") || errorMsg.includes("exists")) {
        throw new Error("User already exists")
      }
      
      // For other errors, throw them
      throw apiError
    }
  },

  async signIn(email: string, password: string) {
    const supabase = getSupabaseClient()

    const { data, error } = await supabase.auth.signInWithPassword({
      email,
      password,
    })

    if (error) throw error
    return data
  },

  async signOut() {
    const supabase = getSupabaseClient()
    const { error } = await supabase.auth.signOut()
    if (error) throw error
  },

  async getCurrentUser() {
    const supabase = getSupabaseClient()
    const {
      data: { user },
      error,
    } = await supabase.auth.getUser()
    if (error) throw error
    return user
  },

  async getSession() {
    const supabase = getSupabaseClient()
    const {
      data: { session },
      error,
    } = await supabase.auth.getSession()
    if (error) throw error
    return session
  },

  async updatePassword(email: string, newPassword: string) {
    const supabase = getSupabaseClient()
    
    try {
      // Use Supabase admin REST API via a server-side endpoint
      // For now, we'll throw an error directing to use server-side update
      const response = await fetch("/api/auth/update-password", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          email,
          password: newPassword,
        }),
      })

      if (!response.ok) {
        // Try to parse error message; if server isn't configured for admin ops,
        // fall back to sending a password reset email to the user (safer for client-only setups).
        let errJson: any = null
        try {
          errJson = await response.json()
        } catch (e) {
          // ignore parse errors
        }

        const errMsg = errJson?.message || null

        if (errMsg && errMsg.toLowerCase().includes("server not configured")) {
          // fallback: send password reset email using client anon key
          const { data, error } = await supabase.auth.resetPasswordForEmail(email)
          if (error) throw error
          return data
        }

        throw new Error(errMsg || "Failed to update password")
      }

      return await response.json()
    } catch (error) {
      throw error
    }
  },
}
